import React, { useState, useContext } from 'react'
import { Form, Button } from 'react-bootstrap'
import AuthService from '../../../services/auth.service'
import UserContext from '../../../context/UserContext'
import GLogin from './GLogin'

const authService = new AuthService()

const LoginPage = (props) => {
  const [loginInfo, setLoginInfo] = useState({
    username: '',
    pwd: '',
  })
  const { storeUser, showText } = useContext(UserContext)

  const handleSubmit = (e) => {
    e.preventDefault()

    authService
      .login(loginInfo.username, loginInfo.pwd)
      .then((res) => {
        storeUser(res.data)
        props.closeModal()
        showText('Sesi√≥n iniciada correctamente üßòüèª‚Äç‚ôÄÔ∏è')
      })
      .catch((err) => showText(err.response.data.message))
  }

  const handleInputChange = (e) => {
    const { name, value } = e.currentTarget

    setLoginInfo({
      ...loginInfo,
      [name]: value,
    })
  }

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Group className="mb-3" controlId="username">
        <Form.Label>Nombre de usuario</Form.Label>
        <Form.Control
          onChange={handleInputChange}
          value={loginInfo.username}
          name="username"
          type="text"
          placeholder="Nombre de usuario"
        />
      </Form.Group>

      <Form.Group className="mb-3" controlId="password">
        <Form.Label>Contrase√±a</Form.Label>
        <Form.Control
          onChange={handleInputChange}
          value={loginInfo.pwd}
          name="pwd"
          type="password"
          placeholder="********"
        />
      </Form.Group>

      <div id="login-btns">
        <Button className="btn-home" variant="primary" type="submit">
          Acceder
        </Button>
        <GLogin />
      </div>
    </Form>
  )
}

export default LoginPage
